.dsc-tag__container {
  display: inline-block;
  border-radius: 4px;
}

.dsc-tag__size-sm {
  font-size: 12px;
  border-width: 1px;
  border-style: solid;
}

.dsc-tag__size-md {
  font-size: 16px;
  border-width: 1.5px;
  border-style: solid;
}

.dsc-tag__size-lg {
  font-size: 18px;
  border-width: 2px;
  border-radius: 8px;
  border-style: solid;
}
