/**
 *
 * Helpful reusable function-like things that will be used to implement
 * styles for components.
 *
 */
.dsc-search-bar__container {
  display: flex;
  padding: .5rem;
  border-radius: 1rem;
}

.dsc-search-bar__container .dsc-input-box {
  border: none;
  flex-grow: 1;
  background: transparent;
  padding-left: .5rem;
  outline: none;
  font-size: 1rem;
  caret-color: #1f2456;
}

.dsc-search-bar__container .dsc-end-icon-box {
  cursor: pointer;
}
