.dsc-btn__container {
  outline: none;
  border: none;
  border-radius: 4px;
  border-color: transparent;
  cursor: pointer;
}

.dsc-btn__size-sm {
  font-size: 12px;
  border-width: 1px;
  border-style: solid;
}

.dsc-btn__size-md {
  font-size: 16px;
  border-width: 1.5px;
  border-style: solid;
}

.dsc-btn__size-lg {
  font-size: 18px;
  border-width: 2px;
  border-radius: 8px;
  border-style: solid;
}
