.dsc-loader__spinner {
  left: 50%;
  margin-left: -20px;
  top: 50%;
  margin-top: -20px;
  position: absolute;
  z-index: 19 !important;
  animation: loading-bar-spinner 600ms linear infinite;
}

.dsc-loader__spinner-sm {
  margin-left: -15px;
  margin-top: -15px;
}

.dsc-loader__spinner--icon-sm {
  width: 30px;
  height: 30px;
  border: solid 2px transparent;
  border-radius: 50%;
}

.dsc-loader__spinner-md {
  margin-left: -20px;
  margin-top: -20px;
}

.dsc-loader__spinner--icon-md {
  width: 40px;
  height: 40px;
  border: solid 4px transparent;
  border-radius: 50%;
}

.dsc-loader__spinner-lg {
  margin-left: -25px;
  margin-top: -25px;
}

.dsc-loader__spinner--icon-lg {
  width: 50px;
  height: 50px;
  border: solid 6px transparent;
  border-radius: 50%;
}

@keyframes loading-bar-spinner {
  0% {
    transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
